#container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    background-image: url(https://wallpaperaccess.com/full/17601.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  #input_text{
    width: 50%;
    max-width: 800px;
    font-size: 14px;
    border: 2px solid salmon;
    font-family: 'Times New Roman', Times, serif;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .input_text{
    height: 3%;
  }

  #check_button{
    margin: 10px 5px;
    width: 100px;
    height: 25px;
    background-color: #dc3545;
    border-color: darkgray black black darkgray ;
  }
  
  #submit_button{
    margin: 10px 5px;
    width: 150px;
    height: 35px;
    background-color: #dc3545;
    border-color: darkgray black black darkgray ;
  }